import React, { useEffect } from "react";
import { useSelector } from "react-redux";

import { Fab, Paper, Stack, Tooltip } from "@mui/material";

import AddIcon from "@mui/icons-material/Add";

import VaultList from "./vault-list";

import PublicAddress from "../components/public-address";
import { useAppDispatch, accountSelector, setSelectedVault } from "../store";

export default function Home() {
  const dispatch = useAppDispatch();
  const { account } = useSelector(accountSelector);
  const { address } = account;

  useEffect(() => {
    dispatch(setSelectedVault(null));
  });

  return (
    <>
      <Stack spacing={2} padding={2}>
        <PublicAddress address={address} abbreviate={true} />
        <Paper variant="outlined">
          <VaultList subheader={false} />
        </Paper>
      </Stack>

      <Tooltip title="Create a vault" placement="left">
        <Fab
          component="a"
          href="#/new"
          color="primary"
          aria-label="add new vault"
          sx={{ position: "absolute", bottom: 16, right: 16 }}
        >
          <AddIcon />
        </Fab>
      </Tooltip>
    </>
  );
}
