import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export type ServerState = {
  url: string;
  changes?: WebSocket;
};

const initialState: ServerState = {
  url: "https://localhost:5053",
  changes: null,
};

const serverSlice = createSlice({
  name: "server",
  initialState,
  reducers: {
    setWebSocket: (state, { payload }: PayloadAction<WebSocket>) => {
      state.changes = payload;
    },
  },
});

export const { setWebSocket } = serverSlice.actions;
export const serverSelector = (state: { server: ServerState }) => state.server;
export default serverSlice.reducer;
