import React from "react";
import { useNavigate } from "react-router-dom";

import {
  List,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  ListSubheader,
  Divider,
} from "@mui/material";

import FileIcon from "@mui/icons-material/FileOpen";
import AccountIcon from "@mui/icons-material/AccountBox";
import NoteIcon from "@mui/icons-material/Article";
import PageIcon from "@mui/icons-material/Web";
import ListIcon from "@mui/icons-material/List";
import KeyIcon from "@mui/icons-material/Key";
import PinIcon from "@mui/icons-material/Pin";

import { WorkerStorageProps, StorageProps } from "../props";

import VaultHeader from "./vault-header";

import { withVault } from "./with-vault";

function ChooseSecretKind(props: StorageProps) {
  const { storage } = props;
  const { summary } = storage;
  const navigate = useNavigate();

  const selectSecretKind = (kind: string) => {
    navigate(`/vault/${summary.id}/new/${kind}`);
  };

  const SubHeader = () => {
    return (
      <>
        <ListSubheader sx={{ paddingLeft: 3 }}>
          Choose secret type
        </ListSubheader>
        <Divider />
      </>
    );
  };

  return (
    <List component="nav" subheader={<SubHeader />} sx={{ padding: 0 }}>
      <ListItemButton
        sx={{ paddingLeft: 3 }}
        onClick={() => selectSecretKind("account")}
      >
        <ListItemIcon>
          <AccountIcon />
        </ListItemIcon>
        <ListItemText
          primary="Account"
          secondary="Store a password for a web service"
        />
      </ListItemButton>
      <ListItemButton
        sx={{ paddingLeft: 3 }}
        onClick={() => selectSecretKind("note")}
      >
        <ListItemIcon>
          <NoteIcon />
        </ListItemIcon>
        <ListItemText
          primary="Secure Note"
          secondary="Create an encrypted note"
        />
      </ListItemButton>
      <ListItemButton
        disabled
        sx={{ paddingLeft: 3 }}
        onClick={() => selectSecretKind("pin")}
      >
        <ListItemIcon>
          <PinIcon />
        </ListItemIcon>
        <ListItemText
          primary="PIN"
          secondary="Store a personal identification number"
        />
      </ListItemButton>
      <ListItemButton
        disabled
        sx={{ paddingLeft: 3 }}
        onClick={() => selectSecretKind("page")}
      >
        <ListItemIcon>
          <PageIcon />
        </ListItemIcon>
        <ListItemText
          primary="Web Page"
          secondary="Write a markdown web page"
        />
      </ListItemButton>
      <ListItemButton
        disabled
        sx={{ paddingLeft: 3 }}
        onClick={() => selectSecretKind("certificate")}
      >
        <ListItemIcon>
          <KeyIcon />
        </ListItemIcon>
        <ListItemText
          primary="Certificate / Key"
          secondary="Save a PEM encoded certificate or private key"
        />
      </ListItemButton>
      <ListItemButton
        sx={{ paddingLeft: 3 }}
        onClick={() => selectSecretKind("list")}
      >
        <ListItemIcon>
          <ListIcon />
        </ListItemIcon>
        <ListItemText
          primary="List"
          secondary="Make a list of secret credentials"
        />
      </ListItemButton>
      <ListItemButton
        sx={{ paddingLeft: 3 }}
        onClick={() => selectSecretKind("file")}
      >
        <ListItemIcon>
          <FileIcon />
        </ListItemIcon>
        <ListItemText primary="File" secondary="Upload a file" />
      </ListItemButton>
    </List>
  );
}

function Unlocked(props: WorkerStorageProps) {
  const { worker, storage } = props;
  return (
    <>
      <VaultHeader editing={false} worker={worker} storage={storage} />
      <ChooseSecretKind storage={storage} />
    </>
  );
}

export default withVault(Unlocked);
