import * as React from "react";
import {
  Alert,
  Stack,
  Box,
  Paper,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Typography,
} from "@mui/material";

import { Summary } from "../../types";
import ConfirmValueForm from "../forms/confirm-value";

interface ConfirmDeleteVaultProps {
  open: boolean;
  summary?: Summary;
  handleCancel: () => void;
  handleOk: (result: Summary) => void;
}

export default function ConfirmDeleteVaultDialog(
  props: ConfirmDeleteVaultProps
) {
  const { open, summary, handleCancel, handleOk } = props;

  if (!summary) {
    return null;
  }

  return (
    <Dialog open={open} onClose={handleCancel}>
      <DialogTitle color="text.secondary">Delete Vault</DialogTitle>
      <DialogContent>
        <Stack spacing={2}>
          <Typography variant="body1" component="div">
            Are you sure you want to permanently delete this vault?
          </Typography>

          <Paper variant="outlined">
            <Box padding={2}>{summary.name}</Box>
          </Paper>

          <Typography variant="body1" component="div">
            Type the vault name to confirm:
          </Typography>

          <ConfirmValueForm
            value={summary.name}
            onFormSubmit={() => handleOk(summary)}
          />

          <Alert severity="warning">
            Deletion is permanent, it cannot be undone.
          </Alert>
        </Stack>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleCancel}>Cancel</Button>
        <Button type="submit" form="confirm-value-form" variant="contained">
          OK
        </Button>
      </DialogActions>
    </Dialog>
  );
}
