import React from "react";
import { useNavigate } from "react-router-dom";

import { Fab, Tooltip } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";

import { StorageProps } from "../props";

export default function NewSecretFab(props: StorageProps) {
  const { storage } = props;
  const navigate = useNavigate();
  const { summary } = storage;

  const newSecret = () => {
    navigate(`/vault/${summary.id}/new`);
  };

  return (
    <Tooltip title="Create a secret" placement="left">
      <Fab
        color="primary"
        aria-label="new secret"
        sx={{ position: "fixed", bottom: 16, right: 16 }}
        onClick={newSecret}
      >
        <AddIcon />
      </Fab>
    </Tooltip>
  );
}
