import React from "react";
import { useSelector } from "react-redux";

import { SecretMeta } from "../../types";
import { accountSelector } from "../../store";
import CheckedInput, {
  CheckedInputProps,
} from "../../components/checked-input";

type SecretLabelProps = {
  onCheck: (meta: SecretMeta[]) => void;
  secretId?: string;
} & Partial<CheckedInputProps>;

export default function SecretLabel(props: SecretLabelProps) {
  const { onCheck, secretId } = props;
  const { account } = useSelector(accountSelector);
  const { cache } = account;

  const checkedInputProps = props as CheckedInputProps;

  const checkValue = async (value: string) => {
    const existingSecrets: SecretMeta[] = await cache.findByLabel(
      value,
      secretId
    );
    onCheck(existingSecrets);
    return existingSecrets.length === 0;
  };

  return (
    <CheckedInput
      id="secret-label"
      label="Name"
      placeholder="Enter a secret name"
      checkValue={checkValue}
      {...checkedInputProps}
    />
  );
}
