import React, { useState } from "react";
import { useSelector } from "react-redux";

import { Stack } from "@mui/material";

import Diceware from "../../diceware";
import { NewVaultResult, VaultWorker, VaultStorage } from "../../types";

import { vaultsSelector } from "../../store";

import CheckedInput from "../../components/checked-input";

interface CreateVaultProps {
  worker: VaultWorker;
  onFormSubmit: (result: NewVaultResult) => void;
}

export default function CreateVaultForm(props: CreateVaultProps) {
  const { vaults } = useSelector(vaultsSelector);
  const { worker } = props;
  const { onFormSubmit } = props;

  const [existing, setExisting] = useState<VaultStorage>(null);
  const [name, setName] = useState("");
  const [nameError, setNameError] = useState(false);
  const [nameHelperText, setNameHelperText] = useState("");

  const [password, setPassword] = useState("");

  const onNameChange = (e: React.ChangeEvent<HTMLInputElement>) =>
    setName(e.target.value);

  const onSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    setNameHelperText("");
    setNameError(false);

    if (name.trim() === "") {
      setNameHelperText("Name may not be empty");
      setNameError(true);
      setName("");
    } else if (existing) {
      setNameHelperText("Vault name is already taken");
      setNameError(true);
    } else {
      onFormSubmit({ name, password });
    }
  };

  const onGenerate = (passphrase: string) => setPassword(passphrase);

  const checkValue = async (value: string) => {
    const existingVault = vaults.find(
      (storage) => storage.summary.name === value
    );

    if (!existingVault && existing) {
      setNameHelperText("");
      setNameError(false);
    }

    setExisting(existingVault);
    return !existingVault;
  };

  return (
    <form id="new-vault-form" onSubmit={onSubmit} noValidate>
      <Stack spacing={2} sx={{ paddingTop: 1 }}>
        <CheckedInput
          id="vault-name"
          label="Name"
          autoFocus
          required
          onChange={onNameChange}
          value={name}
          error={nameError}
          helperText={nameHelperText}
          checkValue={checkValue}
          placeholder="Name for the vault"
        />

        <Diceware onGenerate={onGenerate} worker={worker} />
      </Stack>
    </form>
  );
}
