import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";

import {
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  ListSubheader,
  Divider,
  Stack,
  IconButton,
  Tooltip,
} from "@mui/material";

import AddIcon from "@mui/icons-material/Add";
import LockIcon from "@mui/icons-material/Lock";
import LockOpenIcon from "@mui/icons-material/LockOpen";

import { AppDispatch } from "../store";
import { vaultsSelector, setSelectedVault, lockVault } from "../store/vaults";
import { VaultStorage, Summary } from "../types";

type VaultListProps = {
  subheader: boolean;
};

export default function VaultList(props: VaultListProps) {
  const { subheader } = props;
  const { vaults, selectedVault } = useSelector(vaultsSelector);
  const navigate = useNavigate();
  const dispatch: AppDispatch = useDispatch();

  const openVault = async (summary: Summary) => {
    // Got an existing selection that may be unlocked
    // so let's lock it
    if (selectedVault && selectedVault.id !== summary.id) {
      const storage = vaults.find(
        (storage) => storage.summary.id === selectedVault.id
      );
      if (storage) {
        await dispatch(lockVault({ storage }));
      }
    }
    await dispatch(setSelectedVault(summary));
    navigate(`/vault/${summary.id}`);
  };

  const SubHeader = () => {
    return subheader ? (
      <>
        <ListSubheader sx={{ paddingRight: 1 }}>
          <Stack
            direction="row"
            justifyContent="space-between"
            alignItems="center"
          >
            Vaults
            <Tooltip title="Create a vault" placement="right">
              <IconButton
                component="a"
                href="#/new"
                sx={{ width: 40, height: 40 }}
              >
                <AddIcon />
              </IconButton>
            </Tooltip>
          </Stack>
        </ListSubheader>
        <Divider />
      </>
    ) : null;
  };

  return (
    <List component="nav" subheader={<SubHeader />} sx={{ padding: 0 }}>
      {vaults.map((vault: VaultStorage) => {
        return (
          <div key={vault.summary.id}>
            <ListItem
              selected={
                (selectedVault && selectedVault.id) === vault.summary.id
              }
              component="div"
              disablePadding
            >
              <ListItemButton onClick={() => openVault(vault.summary)}>
                <ListItemIcon>
                  {vault.locked ? <LockIcon /> : <LockOpenIcon />}
                </ListItemIcon>
                <ListItemText primary={vault.summary.name} />
              </ListItemButton>
            </ListItem>
            <Divider light />
          </div>
        );
      })}
    </List>
  );
}
