import React, { Component } from "react";
import { connect } from "react-redux";
import { NavigateFunction } from "react-router-dom";
import { RootState, AppDispatch } from "../store";

import { refresh } from "../store/account";
import { setWebSocket } from "../store/server";
import { refreshVault } from "../store/vaults";
import { VaultStorage, Account, ChangeAction, Summary } from "../types";

type WebSocketMessageEvent = {
  data: Blob;
};

type ChangesState = {
  url?: string;
};

type ChangesProps = {
  dispatch: AppDispatch;
  account: Account;
  websocket?: WebSocket;
  vaults: VaultStorage[];
  selectedVault: Summary;
  navigate: NavigateFunction;
};

class Changes extends Component<ChangesProps, ChangesState> {
  onMessage: (e: WebSocketMessageEvent) => void;

  constructor(props: ChangesProps) {
    super(props);

    this.state = { url: null };

    // The message listener must be bound to this
    // scope so we can remove it correctly
    this.onMessage = this.handleMessage.bind(this);
  }

  componentDidMount() {
    const { account, websocket, dispatch } = this.props;
    const { cache } = account;

    const getChangesWebSocket = async () => {
      if (websocket == null) {
        // Store the websocket in the state so
        // we can close it explicitly on logout
        const url = await cache.changesFeedUrl();
        dispatch(setWebSocket(new WebSocket(url)));
      }
    };

    getChangesWebSocket();
  }

  componentWillUnmount() {
    const { websocket } = this.props;
    if (websocket) {
      websocket.close();
    }
  }

  async handleMessage(e: WebSocketMessageEvent) {
    const { account, vaults, selectedVault, dispatch, navigate } = this.props;
    const { cache } = account;

    const buffer = await e.data.arrayBuffer();
    const data = Array.from(new Uint8Array(buffer));
    const [selfChange, actions]: [boolean, ChangeAction[]] =
      await cache.handleChange(data);

    const currentVaultId = selectedVault && selectedVault.id;

    console.log("selfChange", selfChange);

    // Only react to changes that were NOT triggered by this node
    if (!selfChange) {
      console.log("change received", vaults);
      console.log("change received", selectedVault);
      console.log("ui change actions", currentVaultId, actions);

      actions.forEach(async (action) => {
        if (action.create) {
          // Reload the vault list
          await dispatch(refresh({ cache }));
        } else if (action.remove) {
          // Reload the vault list
          await dispatch(refresh({ cache }));

          if (action.remove === currentVaultId) {
            console.log("handle remove CURRENT vault");
            // Close the vault view, navigate to /
            navigate(`/`);
          }
        } else if (currentVaultId && action.pull === currentVaultId) {
          console.log("pull the current vault!!!");

          // Pull changes from the remote node
          await cache.pull(false);

          console.log("pull completed...");

          const storage = vaults.find(
            (storage) => storage.summary.id === currentVaultId
          );

          // Update the view of the vault meta data
          await dispatch(refreshVault({ account, storage }));

          console.log("pull refresh completed...");

          // Navigate to the vault in case a currently visible
          // secret has been changed
          navigate(`/vault/${currentVaultId}?now=${Date.now()}`);
        }
      });
    }
  }

  render() {
    const { websocket } = this.props;
    //const { url } = this.state;

    // Must update the listener when props change so
    // it can see the new props
    if (websocket) {
      websocket.removeEventListener("message", this.onMessage);
      websocket.addEventListener("message", this.onMessage);
    }

    //if (url) {
    //console.log("navigate to", url);
    //return <Navigate to={url} />
    //}

    // NOTE: returning null here annoys the typescript typechecker
    return <></>;
  }
}

const ConnectedChanges = connect(
  (root: RootState) => {
    return {
      account: root.account.account,
      websocket: root.server.changes,
      vaults: root.vaults.vaults,
      selectedVault: root.vaults.selectedVault,
    };
  },
  (dispatch: AppDispatch) => {
    return { dispatch };
  }
)(Changes);

export default ConnectedChanges;
