import React, { useState } from "react";

import { Button, Stack, TextField, Input } from "@mui/material";

import { SecretMeta, SecretData, SecretKind, FileSecret } from "../../types";

import FileUploadReader from '../../components/file-upload-reader';

import SecretLabel from "./secret-label";

// 8MB for file uploads
const MAX_FILE_SIZE = 8388608;

type FileUploadFormProps = {
  onFormSubmit: (result: SecretData) => void;
  secret?: SecretData;
};

export default function FileUploadForm(props: FileUploadFormProps) {
  const { onFormSubmit, secret } = props;

  const initialLabel = secret && secret.meta.label;
  const initialName = secret && (secret.secret as FileSecret).name;
  const initialBuffer = secret && (secret.secret as FileSecret).buffer;
  const initialMime = secret && (secret.secret as FileSecret).mime;

  const [label, setLabel] = useState(initialLabel || "");
  const [labelError, setLabelError] = useState(false);
  const [labelHelperText, setLabelHelperText] = useState(null);
  const [selectedFile, setSelectedFile] = useState(null);

  const [existingSecrets, setExistingSecrets] = useState<SecretMeta[]>([]);

  const [fileError, setFileError] = useState(false);

  const onLabelChange = (e: React.ChangeEvent<HTMLInputElement>) =>
    setLabel(e.target.value);

  const onLabelCheck = (secrets: SecretMeta[]) => {
    if (secrets.length === 0) {
      setLabelError(false);
      setLabelHelperText(null);
    }
    setExistingSecrets(secrets);
  };

  const onFileSelect = async (file: File) => {
    setSelectedFile(file);
    if (label === '') {
      setLabel(file.name);
    }
  }

  const onSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    setLabelError(false);
    setLabelHelperText(null);
    setFileError(false);

    const name = selectedFile && selectedFile.name || initialName || '';
    const mime = selectedFile && selectedFile.type || initialMime || 'application/octet-stream';
    const size = selectedFile && selectedFile.size
      || initialBuffer && initialBuffer.length;

    if (label.trim() === "") {
      setLabelError(true);
      setLabel("");
    } else if (name.trim() === "") {
      setFileError(true);
    } else if (size && size > MAX_FILE_SIZE) {
      setFileError(true);
    } else if (existingSecrets.length > 0) {
      setLabelError(true);
      setLabelHelperText("Secret name already exists");
    } else if (!initialBuffer && !selectedFile) {
      setFileError(true);
    } else {
      const buffer = selectedFile
        && Array.from(new Uint8Array(await selectedFile.arrayBuffer())) || initialBuffer;

      //console.log("submit", { name, mime });
      //console.log("submit file", buffer.length);
      //console.log(buffer);

      const info: SecretData = {
        secretId: secret && secret.secretId,
        meta: {
          label,
          kind: SecretKind.File,
        },
        secret: { buffer, name, mime },
      };
      onFormSubmit(info);
    }
  };


  return (
    <form id="file-upload-form" onSubmit={onSubmit} noValidate>
      <Stack spacing={2} sx={{ paddingTop: 1 }}>
        <SecretLabel
          autoFocus
          required
          onCheck={onLabelCheck}
          secretId={secret && secret.secretId}
          onChange={onLabelChange}
          value={label}
          error={labelError}
          helperText={labelHelperText}
        />

        <FileUploadReader onSelect={onFileSelect} />

      </Stack>
    </form>
  );
}
