import React, { useState } from "react";
import { Paper, InputBase, IconButton } from "@mui/material";
import SearchIcon from "@mui/icons-material/Search";
import CancelIcon from "@mui/icons-material/Cancel";

type SearchInputProps = {
  value: string;
  onSearchChange: (value: string) => void;
  placeholder: string;
  ariaLabel: string;
};

export default function SearchInput(props: SearchInputProps) {
  const { value, onSearchChange, placeholder, ariaLabel } = props;
  const [cancelVisible, setCancelVisible] = useState(value !== '');

  const cancelDisplay = cancelVisible ? "visible" : "hidden";

  const onSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
  };

  const onSearch = (value: string) => {
    //setValue(value);
    setCancelVisible(value !== "");
    onSearchChange(value);
  };

  const onChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    onSearch(event.target.value);
  };

  const clearSearch = () => onSearch("");

  return (
    <Paper
      elevation={0}
      component="form"
      sx={{
        paddingTop: 1,
        paddingBottom: 1,
        display: "flex",
        alignItems: "center",
        marginRight: 1,
      }}
      onSubmit={onSubmit}
    >
      <SearchIcon sx={{ marginRight: 3 }} />
      <InputBase
        sx={{ ml: 1, flex: 1, fontSize: ".875rem" }}
        placeholder={placeholder}
        value={value}
        onChange={onChange}
        inputProps={{ "aria-label": ariaLabel }}
      />
      <IconButton
        sx={{ marginRight: 1, visibility: cancelDisplay }}
        onClick={clearSearch}
      >
        <CancelIcon />
      </IconButton>
    </Paper>
  );
}
