import React, { useState } from "react";

import { Stack, IconButton, Input, Tooltip } from "@mui/material";

import ContentCopyIcon from "@mui/icons-material/ContentCopy";

import PasswordAdornment from "./password-adornment";

import { copyToClipboard } from "../../utils";

interface ReadOnlyPasswordProps {
  value: string;
  showCopy: boolean;
}

export default function ReadOnlyPassword(props: ReadOnlyPasswordProps) {
  const { value, showCopy } = props;
  const [showPassword, setShowPassword] = useState(false);

  const copy = showCopy ? (
    <Tooltip title="Copy to clipboard">
      <IconButton onClick={async () => await copyToClipboard(value)}>
        <ContentCopyIcon />
      </IconButton>
    </Tooltip>
  ) : null;

  return (
    <Stack spacing={2} direction="row" flex={1}>
      <Input
        disabled
        type={showPassword ? "text" : "password"}
        value={value}
        autoComplete="off"
        sx={{ width: "100%" }}
        endAdornment={<PasswordAdornment onChange={setShowPassword} />}
      />
      {copy}
    </Stack>
  );
}
