import * as React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Typography,
  Stack,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from "@mui/material";

import PasswordForm from "../forms/password-form";

import { AppDispatch } from "../../store";
import { accountSelector } from "../../store/account";
import { setSnackbar } from "../../store/snackbar";

interface VerifyPasswordProps {
  open: boolean;
  handleCancel: () => void;
  handleOk: (result: string) => void;
}

export default function VerifyPasswordDialog(props: VerifyPasswordProps) {
  const dispatch: AppDispatch = useDispatch();
  const { account } = useSelector(accountSelector);
  const { cache } = account;

  const { open, handleCancel, handleOk } = props;

  const onHandleOk = async (passphrase: string) => {
    let verified = false;
    try {
      await cache.verify(passphrase);
      verified = true;
      /* eslint-disable no-empty */
    } catch (e) {}

    if (verified) {
      handleOk(passphrase);
    } else {
      dispatch(
        setSnackbar({
          message: `Passphrase verification failed`,
          severity: "error",
        })
      );
    }
  };

  return (
    <Dialog open={open} onClose={handleCancel}>
      <DialogTitle color="text.secondary">Confirm Passphrase</DialogTitle>
      <DialogContent>
        <Stack spacing={2}>
          <Typography variant="body1" color="text.secondary" gutterBottom>
            To continue you must confirm the encryption passphrase for this
            vault.
          </Typography>
          <PasswordForm onFormSubmit={onHandleOk} autoFocus />
        </Stack>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleCancel}>Cancel</Button>
        <Button type="submit" form="password-form" variant="contained">
          OK
        </Button>
      </DialogActions>
    </Dialog>
  );
}
