import React, { useState } from "react";

import Stack from "@mui/material/Stack";
import TextField from "@mui/material/TextField";

import SecretLabel from "./secret-label";

import { SecretMeta, SecretData, SecretKind } from "../../types";

interface SecureNoteFormProps {
  onFormSubmit: (result: SecretData) => void;
  secret?: SecretData;
}

export default function SecureNoteForm(props: SecureNoteFormProps) {
  const { onFormSubmit, secret } = props;

  const initialLabel = secret && secret.meta.label;
  const initialNote = secret && (secret.secret as string);

  const [label, setLabel] = useState(initialLabel || "");
  const [labelError, setLabelError] = useState(false);
  const [labelHelperText, setLabelHelperText] = useState(null);

  const [existingSecrets, setExistingSecrets] = useState<SecretMeta[]>([]);

  const [note, setNote] = useState(initialNote || "");
  const [noteError, setNoteError] = useState(false);

  const onLabelChange = (e: React.ChangeEvent<HTMLInputElement>) =>
    setLabel(e.target.value);

  const onLabelCheck = (secrets: SecretMeta[]) => {
    if (secrets.length === 0) {
      setLabelError(false);
      setLabelHelperText(null);
    }
    setExistingSecrets(secrets);
  };

  const onNoteChange = (e: React.ChangeEvent<HTMLInputElement>) =>
    setNote(e.target.value);

  const onSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    setLabelError(false);
    setLabelHelperText(null);
    setNoteError(false);

    if (label.trim() === "") {
      setLabelError(true);
      setLabel("");
    } else if (existingSecrets.length > 0) {
      setLabelError(true);
      setLabelHelperText("Secret name already exists");
    } else if (note.trim() === "") {
      setNoteError(true);
      setNote("");
    } else {
      const info: SecretData = {
        secretId: secret && secret.secretId,
        meta: {
          label,
          kind: SecretKind.Note,
        },
        secret: note,
      };
      onFormSubmit(info);
    }
  };

  return (
    <form id="secure-note-form" onSubmit={onSubmit} noValidate>
      <Stack spacing={2} sx={{ paddingTop: 1 }}>
        <SecretLabel
          autoFocus
          required
          onCheck={onLabelCheck}
          secretId={secret && secret.secretId}
          onChange={onLabelChange}
          value={label}
          error={labelError}
          helperText={labelHelperText}
        />

        <TextField
          id="secure-note"
          label="Note"
          placeholder="Enter a note"
          required
          multiline
          rows={6}
          onChange={onNoteChange}
          value={note}
          error={noteError}
        />
      </Stack>
    </form>
  );
}
