import React, { useState, useEffect } from "react";

import {
  FormControl,
  FormHelperText,
  OutlinedInput,
  InputLabel,
} from "@mui/material";

import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import ErrorIcon from "@mui/icons-material/Error";

export type CheckedInputProps = {
  id: string;
  value: string;
  label?: string;
  placeholder?: string;
  required?: boolean;
  error?: boolean;
  autoFocus?: boolean;
  helperText?: string;
  checkValue: (value: string) => Promise<boolean>;
  debounceInterval?: number;
  onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void;
};

export default function CheckedInput(props: CheckedInputProps) {
  const {
    id,
    label,
    placeholder,
    required,
    error,
    autoFocus,
    helperText,
    checkValue,
    value,
    onChange,
  } = props;
  const debounceInterval = props.debounceInterval || 50;
  const [valid, setValid] = useState(null);
  const [timeoutInterval, setTimeoutInterval] = useState(null);
  const [initialized, setInitialized] = useState(false);

  useEffect(() => {
    const checkValid = async () => {
      if (value) {
        setValid(await checkValue(value));
        setInitialized(true);
      }
    };

    // WARN: must wait a while here otherwise the logic
    // WARN: to check the initial value that calls out to
    // WARN: findByLabel() will panic as it can't acquire a read
    // WARN: lock on the node cache
    setTimeout(checkValid, debounceInterval);
  }, [value]);

  const visibility = initialized && value !== "" ? "visible" : "hidden";
  const endAdornment = valid ? (
    <CheckCircleIcon color="success" sx={{ visibility }} />
  ) : (
    <ErrorIcon color="error" sx={{ visibility }} />
  );

  const helper =
    error && helperText ? <FormHelperText>{helperText}</FormHelperText> : null;

  return (
    <FormControl required={required} error={error} variant="outlined">
      <InputLabel error={error} htmlFor={id}>
        {label}
      </InputLabel>
      <OutlinedInput
        id={id}
        type="text"
        value={value}
        label={label}
        error={error}
        autoFocus={autoFocus}
        required={required}
        placeholder={placeholder}
        autoComplete="off"
        onChange={onChange}
        endAdornment={endAdornment}
      />
      {helper}
    </FormControl>
  );
}
