import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { WebCache } from "@saveoursecrets/wasm-bindings";

import { VaultWorker, Account, KeyStore, Summary } from "../types";
import { setVaults, setSelectedVault } from "./vaults";
import { AppDispatch, RootState } from ".";

type RefreshRequest = {
  cache: WebCache;
};

// Refresh the list of vaults for this account.
export const refresh = createAsyncThunk<
  WebCache,
  RefreshRequest,
  { dispatch: AppDispatch; getState: () => RootState }
>("account/refresh", async (request, { dispatch, getState }) => {
  const { cache } = request;

  const state = getState() as RootState;
  const { vaults } = state.vaults;

  const summaries = await cache.listVaults();
  const newVaults = summaries
    .sort((a: Summary, b: Summary) => {
      if (a.name < b.name) {
        return -1;
      }
      if (a.name > b.name) {
        return 1;
      }
      return 0;
    })
    .map((summary: Summary) => {
      const vault = vaults.find((v) => v.summary.id === summary.id);
      if (vault) {
        return { ...vault, summary };
      } else {
        return { summary, locked: true };
      }
    });

  await dispatch(setVaults(newVaults));

  return cache;
});

type LoginRequest = {
  worker: VaultWorker;
  url: string;
  keystore: KeyStore;
  passphrase: string;
};

export const login = createAsyncThunk<
  Account,
  LoginRequest,
  { dispatch: AppDispatch }
>("account/login", async (request, { dispatch }) => {
  const { worker, url, keystore, passphrase } = request;

  /* eslint-disable @typescript-eslint/no-explicit-any */
  const cache: WebCache = await new (worker.WebCache as any)();

  await cache.connect(url, keystore, passphrase);

  // Load the list of available vaults
  await dispatch(refresh({ cache }));

  const { address } = keystore;
  const account = { address, cache };

  return account;
});

export const logout = createAsyncThunk<
  null,
  undefined,
  { dispatch: AppDispatch; getState: () => RootState }
>("account/logout", async (request, { dispatch, getState }) => {
  const state = getState() as RootState;
  const { account } = state.account;
  const { changes } = state.server;

  if (changes) {
    changes.close();
  }

  await account.cache.closeVault();

  await dispatch(setVaults([]));
  await dispatch(setSelectedVault(null));
  return null;
});

export interface AccountState {
  account?: Account;
}

const initialState: AccountState = {
  account: null,
};

const accountSlice = createSlice({
  name: "account",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder.addCase(login.fulfilled, (state, action) => {
      state.account = action.payload;
    });
    builder.addCase(logout.fulfilled, (state, action) => {
      state.account = action.payload;
    });
  },
});

export const accountSelector = (state: { account: AccountState }) =>
  state.account;
export default accountSlice.reducer;
