import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

import { Stack, Typography } from "@mui/material";
import { WorkerProps } from "../props";

import PasswordForm from "../authenticated/forms/password-form";
import { AppDispatch } from "../store";
import { login } from "../store/account";
import { serverSelector } from "../store/server";
import { setSnackbar } from "../store/snackbar";

import { signinSelector } from "../store/signin";

export default function VerifyPassphrase(props: WorkerProps) {
  const dispatch: AppDispatch = useDispatch();
  const navigate = useNavigate();
  const { url } = useSelector(serverSelector);
  const { keystore } = useSelector(signinSelector);
  const { worker } = props;

  const verifyPassphrase = async (passphrase: string) => {
    try {
      const loginRequest = {
        worker,
        url,
        keystore,
        passphrase,
      };
      await dispatch(login(loginRequest));
      navigate("/");

      dispatch(
        setSnackbar({
          message: "Keystore passphrase verified",
          severity: "success",
        })
      );
    } catch (e) {
      console.error(e);
      dispatch(
        setSnackbar({
          message: `Keystore verification failed`,
          severity: "error",
        })
      );
    }
  };

  return (
    <Stack spacing={4}>
      <Stack>
        <Typography variant="body1">
          Enter the passphrase for the keystore
        </Typography>
        <Typography variant="body2" color="text.secondary">
          To decrypt and verify your keystore file
        </Typography>
      </Stack>
      <PasswordForm
        onFormSubmit={verifyPassphrase}
        submitLabel="Verify"
        autoFocus
      />
    </Stack>
  );
}
