import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

import {
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  ListSubheader,
  Divider,
  Stack,
  Typography,
} from "@mui/material";

import { SecretMeta } from "../types";
import { VaultProps } from "../props";
import { searchSelector, setQuery, setSearchResults } from "../store/search";

import SearchInput from "../components/search-input";
import SecretIcon from "./secret-icon";

type SecretListProps = {
  maxWidth?: string | number;
  secretId?: string;
} & VaultProps;

export default function SecretList(props: SecretListProps) {
  const { storage, maxWidth } = props;

  if (!storage) {
    return null;
  }

  const { meta } = storage;
  if (meta.length === 0) {
    return (
      <Stack
        marginTop={4}
        alignItems="center"
        direction="column"
        justifyContent="center"
      >
        <Typography paragraph>No secrets yet</Typography>
      </Stack>
    );
  }

  const SubHeader = () => {
    return (
      <>
        <ListSubheader sx={{ paddingRight: 1, paddingLeft: 3 }}>
          <ListSearch {...props} documents={meta} />
        </ListSubheader>
        <Divider />
      </>
    );
  };

  //secondary={toDateTime(fromSerdeDate(meta.lastUpdated))}

  return (
    <List
      component="nav"
      sx={{ padding: 0, maxWidth: maxWidth }}
      subheader={<SubHeader />}
    >
      <Listing {...props} documents={meta} />
    </List>
  );
}

type ListSearchProps = {
  documents: [string, SecretMeta][];
} & VaultProps;

function ListSearch(props: ListSearchProps) {
  const dispatch = useDispatch();
  const { account, documents } = props;
  const { query, results } = useSelector(searchSelector);
  const listing = results || documents;
  const { cache } = account;

  const onSearchChange = async (value: string) => {
    dispatch(setQuery(value));
    if (value !== "") {
      const query = value.trim();
      if (query !== "") {
        const docs = await cache.queryMap(query);
        dispatch(setSearchResults(docs));
      }
    } else {
      dispatch(setSearchResults(null));
    }
  };

  return (
    <SearchInput
      value={query}
      placeholder={`Search secrets (${listing.length})`}
      ariaLabel="search secrets"
      onSearchChange={onSearchChange}
    />
  );
}

type ListingProps = {
  documents: [string, SecretMeta][];
  secretId?: string;
} & VaultProps;

function Listing(props: ListingProps) {
  const { vaultId, documents } = props;

  const navigate = useNavigate();
  const { results } = useSelector(searchSelector);
  const listing = results || documents;

  const showSecret = (id: string) => {
    const path = `/vault/${vaultId}/${id}`;
    navigate(path);
    window.scrollTo(0, 0);
  };

  return (
    <>
      {listing.map((value: [string, SecretMeta]) => {
        const [secretId, meta] = value;
        return (
          <div key={secretId}>
            <ListItem
              component="div"
              selected={props.secretId === secretId}
              disablePadding
              onClick={() => showSecret(secretId)}
              sx={{ overflow: "hidden", paddingLeft: 1 }}
            >
              <ListItemButton>
                <ListItemIcon>
                  <SecretIcon kind={meta.kind} />
                </ListItemIcon>
                <ListItemText
                  primary={meta.label}
                  primaryTypographyProps={{ noWrap: true }}
                />
              </ListItemButton>
            </ListItem>
            <Divider light />
          </div>
        );
      })}
    </>
  );
}
