import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";

import { Button, Stack, Typography } from "@mui/material";

import { AppDispatch } from "../store";
import { signupSelector } from "../store/signup";
import { login } from "../store/account";
import { setSelectedVault } from "../store/vaults";
import { setSnackbar } from "../store/snackbar";
import { serverSelector } from "../store/server";
import PublicAddress from "../components/public-address";

import { WorkerProps } from "../props";

export default function Finish(props: WorkerProps) {
  const navigate = useNavigate();
  const dispatch: AppDispatch = useDispatch();
  const { worker } = props;
  const { url } = useSelector(serverSelector);
  const { signup, address } = useSelector(signupSelector);

  const saveVault = async () => {
    try {
      await signup.createClient(url);

      const [statusCode, summary] = await signup.createAccount();
      if (statusCode !== 200) {
        throw new Error("Could not create account, status code: " + statusCode);
      }

      const [keystore, passphrase] = await signup.takeKeyStore();

      const loginRequest = {
        worker,
        url,
        keystore,
        passphrase,
      };
      await dispatch(login(loginRequest));
      await dispatch(setSelectedVault(summary));
      navigate(`/vault/${summary.id}`);
      dispatch(
        setSnackbar({ message: "Account created", severity: "success" })
      );
    } catch (e) {
      dispatch(setSnackbar({ message: "" + e, severity: "error" }));
    }
  };

  return (
    <Stack spacing={4}>
      <Stack>
        <Typography variant="h3">Save Vault</Typography>
        <PublicAddress address={address} abbreviate={true} />
      </Stack>
      <Stack spacing={4}>
        <Stack>
          <Typography variant="body1">
            Just one more step to complete the signup.
          </Typography>
          <Typography variant="body2" color="text.secondary">
            Send your login vault to be saved so you can share it across
            different devices.
          </Typography>
        </Stack>
        <Button variant="contained" onClick={saveVault}>
          Save Vault
        </Button>
      </Stack>
    </Stack>
  );
}
