import React from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
//import type { PayloadAction } from '@reduxjs/toolkit';

import { Alert, Button, Stack, Typography } from "@mui/material";

import { AccountProps } from "../props";
import { AppDispatch, createNewVault, setSelectedVault } from "../store";

import { NewVaultResult } from "../types";

import CreateVaultForm from "./forms/create-vault";
import { withAccount } from "./with-vault";

function Authenticated(props: AccountProps) {
  const { worker } = props;
  const dispatch: AppDispatch = useDispatch();
  const navigate = useNavigate();

  const onFormSubmit = async (result: NewVaultResult) => {
    // TODO: fix the typing here?
    const action = await dispatch(createNewVault({ result }));

    const { summary } = action.payload;
    await dispatch(setSelectedVault(summary));
    navigate(`/vault/${summary.id}`);
  };

  return (
    <Stack padding={3} spacing={2}>
      <Typography variant="h4">New Vault</Typography>
      <CreateVaultForm onFormSubmit={onFormSubmit} worker={worker} />
      <Alert sx={{ marginTop: 2 }} severity="warning">
        You must memorize the passphrase for your new vault
      </Alert>
      <Button type="submit" form="new-vault-form" variant="contained">
        Create
      </Button>
    </Stack>
  );
}

export default withAccount(Authenticated);
