import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";

import Button from "@mui/material/Button";
import Snackbar from "@mui/material/Snackbar";
import Alert from "@mui/material/Alert";
import Stack from "@mui/material/Stack";

import { updateVault } from "../store/vaults";
import { accountSelector } from "../store/account";

import { VaultStorage, UnlockVaultResult, VaultWorker } from "../types";
import UnlockVaultForm from "./unlock-vault-form";

type VaultViewProps = {
  worker: VaultWorker;
  storage: VaultStorage;
};

export default function UnlockVault(props: VaultViewProps) {
  const { storage } = props;
  //const { vault } = storage;
  const [invalid, setInvalid] = useState(false);
  const dispatch = useDispatch();
  const { account } = useSelector(accountSelector);

  const onFormSubmit = async (result: UnlockVaultResult) => {
    const { password } = result;
    try {
      const { cache } = account;
      // Select and unlock the target vault
      await cache.openVault(storage.summary, password);
      // Load the meta data for the vault
      const meta = await cache.getVaultMeta();
      const newStorage = { ...storage, meta, locked: false };
      dispatch(updateVault(newStorage));
    } catch (e) {
      console.error(e);
      setInvalid(true);
    }
  };

  const hideInvalid = () => setInvalid(false);

  return (
    <>
      <Stack spacing={2} padding={2}>
        <UnlockVaultForm onFormSubmit={onFormSubmit} />
        <Button type="submit" form="unlock-vault-form">
          Unlock
        </Button>
      </Stack>
      <Snackbar open={invalid} autoHideDuration={6000} onClose={hideInvalid}>
        <Alert onClose={hideInvalid} severity="error">
          Invalid password
        </Alert>
      </Snackbar>
    </>
  );
}
