import React, { useState } from "react";

import { Stack, Typography } from "@mui/material";

import { WorkerProps } from "../props";

import UploadKey from "./upload-key";
import VerifyPassphrase from "./verify-passphrase";

enum SigninStep {
  UPLOAD_KEY = 1,
  VERIFY_PASSPHRASE = 2,
}

const signupSteps = [SigninStep.UPLOAD_KEY, SigninStep.VERIFY_PASSPHRASE];

export type StepProps = {
  nextStep: () => void;
} & WorkerProps;

function SigninStepView(props: WorkerProps) {
  const { worker } = props;
  const [stepIndex, setStepIndex] = useState(0);
  const nextStep = () => {
    if (stepIndex < signupSteps.length - 1) {
      setStepIndex(stepIndex + 1);
    }
  };
  const step = signupSteps[stepIndex];

  switch (step) {
    case SigninStep.UPLOAD_KEY:
      return <UploadKey worker={worker} nextStep={nextStep} />;
    case SigninStep.VERIFY_PASSPHRASE:
      return <VerifyPassphrase worker={worker} />;
  }
}

export default function SigninView(props: WorkerProps) {
  const { worker } = props;
  return (
    <Stack padding={3}>
      <Stack>
        <Typography variant="h3" gutterBottom>
          Sign in
        </Typography>
      </Stack>
      <SigninStepView worker={worker} />
    </Stack>
  );
}
