import React from "react";
import { useNavigate, NavigateFunction } from "react-router-dom";

import { Stack, Button, Typography } from "@mui/material";

import AccountPasswordForm from "./forms/account-password";
import FileUpload from "./forms/file-upload";
import CredentialsForm from "./forms/credentials";
import SecureNoteForm from "./forms/secure-note";

import { SecretProps } from "../props";
import { SecretData, SecretKind } from "../types";
import { AppDispatch, useAppDispatch } from "../store";
import { updateSecret } from "../store/vaults";
import { setSnackbar } from "../store/snackbar";

import VaultHeader from "./vault-header";

import { withVaultAndSecret } from "./with-vault";

function makeEditHandler(
  props: SecretProps,
  dispatch: AppDispatch,
  navigate: NavigateFunction
): [SecretData, (result: SecretData) => void] {
  const { account, vaultId, secretId, meta, secret, storage } = props;

  const onFormSubmit = async (result: SecretData) => {
    await dispatch(updateSecret({ result, account, storage }));
    dispatch(
      setSnackbar({
        message: "Secret updated",
        severity: "success",
      })
    );
    navigate(`/vault/${vaultId}/${secretId}?now=${Date.now()}`);
  };

  return [{ secretId, meta, secret }, onFormSubmit];
}

function AccountEditor(props: SecretProps) {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const [secretData, onFormSubmit] = makeEditHandler(props, dispatch, navigate);

  return (
    <>
      <AccountPasswordForm secret={secretData} onFormSubmit={onFormSubmit} />
      <Button type="submit" form="account-password-form" variant="contained">
        Save
      </Button>
    </>
  );
}

function NoteEditor(props: SecretProps) {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const [secretData, onFormSubmit] = makeEditHandler(props, dispatch, navigate);

  return (
    <>
      <SecureNoteForm secret={secretData} onFormSubmit={onFormSubmit} />
      <Button type="submit" form="secure-note-form" variant="contained">
        Save
      </Button>
    </>
  );
}

function ListEditor(props: SecretProps) {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const [secretData, onFormSubmit] = makeEditHandler(props, dispatch, navigate);

  return (
    <>
      <CredentialsForm secret={secretData} onFormSubmit={onFormSubmit} />
      <Button type="submit" form="credentials-form" variant="contained">
        Save
      </Button>
    </>
  );
}

function FileEditor(props: SecretProps) {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const [secretData, onFormSubmit] = makeEditHandler(props, dispatch, navigate);

  return (
    <>
      <FileUpload secret={secretData} onFormSubmit={onFormSubmit} />
      <Button type="submit" form="file-upload-form" variant="contained">
        Save
      </Button>
    </>
  );
}

function Editor(props: SecretProps) {
  const { meta } = props;
  switch (meta.kind) {
    case SecretKind.Account:
      return <AccountEditor {...props} />;
    case SecretKind.Note:
      return <NoteEditor {...props} />;
    case SecretKind.Credentials:
      return <ListEditor {...props} />;
    case SecretKind.File:
      return <FileEditor {...props} />;
  }
}

function Unlocked(props: SecretProps) {
  const { worker, storage } = props;
  return (
    <>
      <VaultHeader editing={false} worker={worker} storage={storage} />
      <Stack padding={3} spacing={2}>
        <Typography variant="h6">Edit</Typography>
        <Editor {...props} />
      </Stack>
    </>
  );
}

export default withVaultAndSecret(Unlocked);
