import { WebCache, Signup } from "@saveoursecrets/wasm-bindings";

export type KeyStore = {
  address: string;
};

export type Nonce = {
  Nonce12?: number[];
  Nonce24?: number[];
};

export type AeadPack = {
  nonce: Nonce;
  ciphertext: number[];
};

export type Account = {
  cache?: WebCache;
  address?: string;
};

export type VaultStorage = {
  summary: Summary;
  locked: boolean;
  meta?: VaultMetaData;
};

export type Summary = {
  version: number;
  id: string;
  name: string;
};

// FIXME: merge these type definitions: SecretDocuments ???
export type VaultMetaData = [string, SecretMeta][];
export type SearchResults = [string, SecretMeta][];

export type SecretData = {
  secretId?: string;
  meta: SecretMeta;
  secret: Secret;
};

export type AccountSecret = {
  account: string;
  url?: string;
  password: string;
};

export type NoteSecret = string;

export type FileSecret = {
  name: string;
  buffer: number[];
  mime?: string;
};

export type CredentialsSecret = {
  [index: string]: string;
};

export type Secret =
  | AccountSecret
  | NoteSecret
  | FileSecret
  | CredentialsSecret;

export enum SecretKind {
  // secret::kind::ACCOUNT
  Account = 1,
  // secret::kind::NOTE
  Note = 2,
  // secret::kind::CREDENTIALS
  Credentials = 3,
  // secret::kind::FILE
  File = 4,
}

export class SecretKindLabel {
  public static toString(kind: SecretKind) {
    switch (kind) {
      case SecretKind.Account:
        return "Account";
      case SecretKind.Note:
        return "Note";
      case SecretKind.Credentials:
        return "Credentials";
      case SecretKind.File:
        return "File";
    }
  }
}

export type VaultWorker = {
  Signup(): Promise<Signup>;
  WebCache(): Promise<WebCache>;
  generatePassphrase(words: number): Promise<[string, number]>;
};

export type SecretReference = {
  secretId: string;
  label: string;
};

export type SecretMeta = {
  label: string;
  kind: SecretKind;
  lastUpdated?: number[];
};

export type NewVaultResult = {
  name: string;
  password: string;
};

export type KeyValueError = {
  key: boolean;
  value: boolean;
};

export type Credentials = {
  [index: string]: string;
};

export type UnlockVaultResult = {
  password: string;
};

export type ChangeAction = {
  pull: string;
  remove: string;
  create: Summary;
};
