import React from "react";
import { useDispatch } from "react-redux";

import { Stack, Typography } from "@mui/material";
import { decode } from "../utils";
import FileUploadReader, { FileBuffer } from "../components/file-upload-reader";
import { AppDispatch } from "../store";
import { setSnackbar } from "../store/snackbar";
import { setKeyStore } from "../store/signin";

import { StepProps } from ".";

export default function UploadKey(props: StepProps) {
  const dispatch: AppDispatch = useDispatch();
  const { nextStep } = props;

  const onFileChange = async (data: FileBuffer) => {
    try {
      const contents = decode(new Uint8Array(data.buffer));
      try {
        const keystore = JSON.parse(contents);
        await dispatch(setKeyStore(keystore));
        nextStep();
      } catch (e) {
        console.error(e);
        dispatch(
          setSnackbar({
            message: `Could not parse file as JSON: ${e.message || ""}`,
            severity: "error",
          })
        );
      }
    } catch (e) {
      console.error(e);
      dispatch(
        setSnackbar({
          message: `Could not decode file as UTF-8: ${e.message || ""}`,
          severity: "error",
        })
      );
    }
  };

  return (
    <Stack spacing={4}>
      <Stack>
        <Typography variant="body1">
          Upload your signing key to verify your identity.
        </Typography>
        <Typography variant="body2" color="text.secondary">
          Your signing key is used to verify you are authorized to make changes
          to an account.
        </Typography>
      </Stack>
      <FileUploadReader onChange={onFileChange} showRead />
    </Stack>
  );
}
