import { useDispatch } from "react-redux";
import { configureStore } from "@reduxjs/toolkit";
import vaultsReducer from "./vaults";
import dialogsReducer from "./dialogs";
import accountReducer from "./account";
import signupReducer from "./signup";
import snackbarReducer from "./snackbar";
import serverReducer from "./server";
import signinReducer from "./signin";
import searchReducer from "./search";

// Re-exports
export * from "./vaults";
export * from "./dialogs";
export * from "./account";
export * from "./signup";
export * from "./snackbar";
export * from "./server";
export * from "./signin";
export * from "./search";

const store = configureStore({
  reducer: {
    vaults: vaultsReducer,
    dialogs: dialogsReducer,
    account: accountReducer,
    signup: signupReducer,
    snackbar: snackbarReducer,
    server: serverReducer,
    signin: signinReducer,
    search: searchReducer,
  },
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({
      serializableCheck: {
        // Ignore these action types
        ignoredActions: [
          "vaults/updateVault",

          "vaults/lockVault/pending",
          "vaults/lockVault/fulfilled",
          "vaults/lockVault/rejected",

          /*
          "vaults/pull/pending",
          "vaults/pull/fulfilled",
          "vaults/pull/rejected",

          "vaults/push/pending",
          "vaults/push/fulfilled",
          "vaults/push/rejected",
          */

          "vaults/create/pending",
          "vaults/create/fulfilled",
          "vaults/create/rejected",

          "vaults/refreshVault/pending",
          "vaults/refreshVault/fulfilled",
          "vaults/refreshVault/rejected",

          "vaults/createSecret/pending",
          "vaults/createSecret/fulfilled",
          "vaults/createSecret/rejected",

          "vaults/readSecret/pending",
          "vaults/readSecret/fulfilled",
          "vaults/readSecret/rejected",

          "vaults/updateSecret/pending",
          "vaults/updateSecret/fulfilled",
          "vaults/updateSecret/rejected",

          "vaults/deleteSecret/pending",
          "vaults/deleteSecret/fulfilled",
          "vaults/deleteSecret/rejected",

          "signup/new/pending",
          "signup/new/fulfilled",
          "signup/new/rejected",

          "signup/delete/pending",
          "signup/delete/fulfilled",
          "signup/delete/rejected",

          "account/login/pending",
          "account/login/fulfilled",
          "account/login/rejected",

          "account/logout/pending",
          "account/logout/fulfilled",
          "account/logout/rejected",

          "account/refresh/pending",
          "account/refresh/fulfilled",
          "account/refresh/rejected",

          "server/setWebSocket",

          "dialogs/setDialogVisible",
        ],
        // Ignore these field paths in all actions
        ignoredActionPaths: [],
        // Ignore these paths in the state
        ignoredPaths: ["vaults", "signup", "account", "server", "dialogs"],
      },
    }),
});

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;

export const useAppDispatch = () => useDispatch<AppDispatch>();

export default store;
