import React from "react";

import { VaultProps } from "../props";

import VaultHeader from "./vault-header";
import SecretList from "./secret-list";
import NewSecretFab from "./new-secret-fab";

import { withVault } from "./with-vault";

function Unlocked(props: VaultProps) {
  const { worker, storage } = props;

  return (
    <>
      <VaultHeader editing={false} worker={worker} storage={storage} />
      <SecretList {...props} />
      <NewSecretFab storage={storage} />
    </>
  );
}

export default withVault(Unlocked);
