import React from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";

import { Divider, IconButton, Stack, Tooltip, Link } from "@mui/material";

import EditIcon from "@mui/icons-material/Edit";
import LockOpenIcon from "@mui/icons-material/LockOpen";

import { AppDispatch } from "../store";
import { lockVault } from "../store/vaults";

import { VaultStorage, VaultWorker } from "../types";

type VaultViewProps = {
  worker: VaultWorker;
  storage: VaultStorage;
  editing: boolean;
};

function VaultActions(props: VaultViewProps) {
  const navigate = useNavigate();

  const { storage, editing } = props;
  const { summary, locked } = storage;

  const onEditVault = () => {
    navigate(`/vault/${summary.id}/edit`);
  };

  const action = !editing ? (
    <Tooltip title="Edit this vault" placement="right">
      <IconButton onClick={onEditVault}>
        <EditIcon />
      </IconButton>
    </Tooltip>
  ) : null;

  return !locked ? action : null;
}

export default function VaultHeader(props: VaultViewProps) {
  const { worker, storage, editing } = props;
  const { summary, locked } = storage;
  const dispatch: AppDispatch = useDispatch();
  const navigate = useNavigate();

  const onLockVault = async () => {
    await dispatch(lockVault({ storage }));
    navigate(`/vault/${summary.id}`);
  };

  const actions = !locked ? (
    <Stack direction="row" alignItems="center" spacing={1}>
      <VaultActions editing={editing} worker={worker} storage={storage} />
      <Tooltip title="Lock this vault" placement="left">
        <IconButton onClick={() => onLockVault()}>
          <LockOpenIcon />
        </IconButton>
      </Tooltip>
    </Stack>
  ) : null;

  return (
    <>
      <Stack
        sx={{
          width: "100%",
          paddingLeft: 3,
          paddingRight: 3,
          paddingTop: 2,
          paddingBottom: 2,
        }}
        direction="row"
        justifyContent="space-between"
      >
        <Link
          href={`#/vault/${summary.id}`}
          variant="h4"
          color="inherit"
          underline="none"
        >
          {summary.name}
        </Link>
        {actions}
      </Stack>
      <Divider />
    </>
  );
}
