import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";

import {
  dialogsSelector,
  setDialogVisible,
  CONFIRM_DELETE_SECRET,
  VERIFY_PASSWORD,
  CONFIRM_DELETE_VAULT,
  CHOOSE_PASSPHRASE,
} from "../../store/dialogs";
import { accountSelector, refresh } from "../../store/account";
import { setSelectedVault } from "../../store/vaults";

import { AppDispatch } from "../../store";

import { vaultsSelector, deleteSecret } from "../../store/vaults";
import { VaultWorker, SecretReference, Summary } from "../../types";

import ConfirmDeleteSecretDialog from "./confirm-delete-secret";
import VerifyPasswordDialog from "./verify-password";
import ConfirmDeleteVaultDialog from "./confirm-delete-vault";
import ChoosePassphraseDialog from "./choose-passphrase";

interface DialogProps {
  worker: VaultWorker;
}

export default function Dialogs(props: DialogProps) {
  const { worker } = props;
  const navigate = useNavigate();
  const dispatch: AppDispatch = useDispatch();
  const { dialogs } = useSelector(dialogsSelector);
  const { vaults, selectedVault } = useSelector(vaultsSelector);
  const { account } = useSelector(accountSelector);
  const { cache } = account;

  const storage = vaults.find(
    (storage) => storage.summary.id === (selectedVault && selectedVault.id)
  );

  const onDeleteSecret = async (result: string) => {
    cancelDialog(CONFIRM_DELETE_SECRET);
    await dispatch(deleteSecret({ result, account, storage }));

    const vaultId = storage.summary.id;
    navigate(`/vault/${vaultId}`);
  };

  const onDeleteVault = async (result: Summary) => {
    cancelDialog(CONFIRM_DELETE_VAULT);
    await cache.removeVault(result);
    await dispatch(refresh({ cache }));
    await dispatch(setSelectedVault(null));
    navigate("/");
  };

  const cancelDialog = async (key: string) => {
    await dispatch(setDialogVisible([key, false, null]));
  };

  return (
    <>
      <ConfirmDeleteSecretDialog
        open={dialogs[CONFIRM_DELETE_SECRET][0] || false}
        handleCancel={() => cancelDialog(CONFIRM_DELETE_SECRET)}
        handleOk={onDeleteSecret}
        secret={dialogs[CONFIRM_DELETE_SECRET][1] as SecretReference}
      />

      <VerifyPasswordDialog
        open={dialogs[VERIFY_PASSWORD][0] || false}
        handleCancel={() => cancelDialog(VERIFY_PASSWORD)}
        handleOk={async (passphrase: string) => {
          await cancelDialog(VERIFY_PASSWORD);
          const callback = dialogs[VERIFY_PASSWORD][1] as (
            value: string
          ) => void;
          callback(passphrase);
        }}
      />

      <ConfirmDeleteVaultDialog
        open={dialogs[CONFIRM_DELETE_VAULT][0] || false}
        handleCancel={() => cancelDialog(CONFIRM_DELETE_VAULT)}
        handleOk={onDeleteVault}
        summary={dialogs[CONFIRM_DELETE_VAULT][1] as Summary}
      />

      <ChoosePassphraseDialog
        worker={worker}
        open={dialogs[CHOOSE_PASSPHRASE][0] || false}
        handleCancel={() => cancelDialog(CHOOSE_PASSPHRASE)}
        handleOk={async (passphrase: string) => {
          await cancelDialog(CHOOSE_PASSPHRASE);
          const callback = dialogs[CHOOSE_PASSPHRASE][1] as (
            value: string
          ) => void;
          callback(passphrase);
        }}
      />
    </>
  );
}
