import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";

import { Stack, Button, Typography } from "@mui/material";

import NoteIcon from "@mui/icons-material/Article";

import { AppDispatch } from "../../store";
import { createSecret } from "../../store/vaults";
import { accountSelector } from "../../store/account";
import { VaultProps } from "../../props";
import { SecretData } from "../../types";
import { withVault } from "../with-vault";

import VaultHeader from "../vault-header";
import SecureNoteForm from "../forms/secure-note";

function Unlocked(props: VaultProps) {
  const { account } = useSelector(accountSelector);
  const { worker, storage, vaultId } = props;
  const dispatch: AppDispatch = useDispatch();
  const navigate = useNavigate();

  const onFormSubmit = async (result: SecretData) => {
    await dispatch(createSecret({ result, account, storage }));
    navigate(`/vault/${vaultId}`);
  };

  return (
    <>
      <VaultHeader editing={false} worker={worker} storage={storage} />
      <Stack padding={3} spacing={2}>
        <Stack direction="row" alignItems="center" spacing={1}>
          <NoteIcon />
          <Typography variant="h6">Secure Note</Typography>
        </Stack>
        <SecureNoteForm onFormSubmit={onFormSubmit} />
        <Button type="submit" form="secure-note-form" variant="contained">
          Save
        </Button>
      </Stack>
    </>
  );
}

export default withVault(Unlocked);
