import {
  createSlice,
  createAsyncThunk,
  PayloadAction,
  isRejected,
  AnyAction,
} from "@reduxjs/toolkit";

import { RootState } from ".";

import {
  NewVaultResult,
  SecretMeta,
  Secret,
  SecretData,
  Account,
  Summary,
  VaultStorage,
} from "../types";

export type VaultState = {
  vaults: VaultStorage[];
  selectedVault?: Summary;
};

const initialState: VaultState = {
  // List of loaded vaults.
  vaults: [],

  // Currently selected vault
  selectedVault: null,
};

type LockVaultRequest = {
  storage: VaultStorage;
};

export const lockVault = createAsyncThunk<
  VaultStorage,
  LockVaultRequest,
  { /* dispatch: AppDispatch, */ getState: () => RootState }
>("vaults/lockVault", async (request, { getState }) => {
  const { storage } = request;
  const newStorage = { ...storage, locked: true };

  const state = getState() as RootState;
  const { account } = state.account;
  await account.cache.closeVault();

  return newStorage;
});

type RefreshVaultRequest = {
  account: Account;
  storage: VaultStorage;
};

export const refreshVault = createAsyncThunk<
  VaultStorage,
  RefreshVaultRequest,
  {
    /* dispatch: AppDispatch */
  }
>("vaults/refreshVault", async (request) => {
  const { account, storage } = request;
  const { cache } = account;
  // Update the vault meta data
  const meta = await cache.getVaultMeta();
  return { ...storage, meta };
});

export type NewVaultRequest = {
  result: NewVaultResult;
};

export const createNewVault = createAsyncThunk<
  VaultStorage,
  NewVaultRequest,
  { getState: () => RootState }
>("vaults/create", async (request, { getState }) => {
  const { result } = request;
  const { name, password } = result;

  const state = getState() as RootState;
  const { account } = state.account;
  const { cache } = account;

  const summary = await cache.createVault(name, password);

  return { summary, locked: true };
});

export type CreateSecretRequest = {
  account: Account;
  result: SecretData;
  storage: VaultStorage;
};

export const createSecret = createAsyncThunk<
  VaultStorage | null,
  CreateSecretRequest,
  {
    /* dispatch: AppDispatch */
  }
>("vaults/createSecret", async (request) => {
  const { result, storage, account } = request;
  const { cache } = account;

  // Create the secret in the memory buffer
  await cache.createSecret(result);

  // Update the vault meta data
  const meta = await cache.getVaultMeta();
  return { ...storage, meta };
});

export type ReadSecretRequest = {
  account: Account;
  secretId: string;
  storage: VaultStorage;
};

export const readSecret = createAsyncThunk<
  [SecretMeta, Secret] | null,
  ReadSecretRequest,
  {
    /* dispatch: AppDispatch */
  }
>("vaults/readSecret", async (request) => {
  const { account, secretId } = request;
  const { cache } = account;
  const result: [SecretMeta, Secret] = await cache.readSecret(secretId);
  const [meta, secret] = result;
  return [meta, secret];
});

export type UpdateSecretRequest = {
  account: Account;
  result: SecretData;
  storage: VaultStorage;
};

export const updateSecret = createAsyncThunk<
  VaultStorage,
  UpdateSecretRequest,
  {
    /* dispatch: AppDispatch */
  }
>("vaults/updateSecret", async (request) => {
  const { result, account, storage } = request;
  const { cache } = account;
  await cache.updateSecret(result);

  // Update the vault meta data
  const meta = await cache.getVaultMeta();

  return { ...storage, meta };
});

export type DeleteSecretRequest = {
  account: Account;
  result: string;
  storage: VaultStorage;
};

export const deleteSecret = createAsyncThunk<
  VaultStorage,
  DeleteSecretRequest,
  {
    /* dispatch: AppDispatch */
  }
>("vaults/deleteSecret", async (request) => {
  const { result, account, storage } = request;
  const { cache } = account;
  await cache.deleteSecret(result);

  // Update the vault meta data
  const meta = await cache.getVaultMeta();

  return { ...storage, meta };
});

const updateVaultFromThunk = (
  state: VaultState,
  action: PayloadAction<VaultStorage | null>
) => {
  const { payload } = action;
  if (payload) {
    state.vaults = state.vaults.map((prop: VaultStorage) => {
      if (payload && payload.summary.id === prop.summary.id) {
        return payload;
      } else {
        return prop;
      }
    });
  }
};

const logError = (state: VaultState, action: AnyAction) => {
  //console.error(action.error);
  throw action.error;
};

const vaultsSlice = createSlice({
  name: "vaults",
  initialState,
  reducers: {
    setVaults: (state, { payload }: PayloadAction<VaultStorage[]>) => {
      state.vaults = payload;
    },
    setSelectedVault: (state, { payload }: PayloadAction<Summary>) => {
      state.selectedVault = payload;
    },
    updateVault: (state, { payload }: PayloadAction<VaultStorage>) => {
      state.vaults = state.vaults.map((prop) => {
        if (payload.summary.id === prop.summary.id) {
          return payload;
        } else {
          return prop;
        }
      });
    },
  },
  extraReducers: (builder) => {
    builder.addCase(createNewVault.fulfilled, (state, action) => {
      state.vaults = [action.payload, ...state.vaults];
    });
    builder.addCase(lockVault.fulfilled, updateVaultFromThunk);
    builder.addCase(refreshVault.fulfilled, updateVaultFromThunk);
    builder.addCase(createSecret.fulfilled, updateVaultFromThunk);
    builder.addCase(updateSecret.fulfilled, updateVaultFromThunk);
    builder.addCase(deleteSecret.fulfilled, updateVaultFromThunk);

    builder.addMatcher(isRejected, logError);
  },
});

export const { setVaults, setSelectedVault, updateVault } = vaultsSlice.actions;
export const vaultsSelector = (state: { vaults: VaultState }) => state.vaults;
export default vaultsSlice.reducer;
