import React, { useState } from "react";
import { Routes, Route, useNavigate } from "react-router-dom";

import { styled, useTheme } from "@mui/material/styles";
import {
  Box,
  Drawer,
  Toolbar,
  Stack,
  Divider,
  IconButton,
  AppBar as MuiAppBar,
  AppBarProps as MuiAppBarProps,
} from "@mui/material";

import MenuIcon from "@mui/icons-material/Menu";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";

import { WorkerProps } from "../props";

import Home from "./home";
import VaultList from "./vault-list";
import Vault from "./vault";
import VaultEdit from "./vault-edit";
import Secret from "./secret";
import Dialogs from "./dialogs";
import AppBarActions from "./app-bar-actions";
import Changes from "./changes";
import BeforeUnload from "./before-unload";
import LogoType from "../logotype";
import NotFound from "../not-found";

import NewVault from "./new-vault";

import NewSecret from "./new-secret";
import SecretAccount from "./secrets/account";
import SecretNote from "./secrets/note";
import SecretList from "./secrets/list";
import SecretFile from "./secrets/file";

import EditSecret from "./edit-secret";

const drawerWidth = 240;

const Main = styled("div", { shouldForwardProp: (prop) => prop !== "open" })<{
  open?: boolean;
}>(({ theme, open }) => ({
  flexGrow: 1,
  padding: 0,
  transition: theme.transitions.create("margin", {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  marginLeft: `-${drawerWidth}px`,
  ...(open && {
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  }),
}));

interface AppBarProps extends MuiAppBarProps {
  open?: boolean;
}

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== "open",
})<AppBarProps>(({ theme, open }) => ({
  transition: theme.transitions.create(["margin", "width"], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: `${drawerWidth}px`,
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const DrawerHeader = styled("div")(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
  justifyContent: "flex-end",
}));

export default function AuthenticatedApp(props: WorkerProps) {
  const { worker } = props;
  const theme = useTheme();
  const navigate = useNavigate();

  const [drawerOpen, setDrawerOpen] = useState(false);

  const handleDrawerOpen = () => setDrawerOpen(true);
  const handleDrawerClose = () => setDrawerOpen(false);

  return (
    <Box sx={{ display: "flex" }}>
      <AppBar position="fixed" open={drawerOpen}>
        <Toolbar>
          <Stack
            sx={{ width: "100%" }}
            direction="row"
            justifyContent="space-between"
          >
            <Stack direction="row" alignItems="center">
              <IconButton
                color="inherit"
                aria-label="open drawer"
                onClick={handleDrawerOpen}
                edge="start"
                sx={{ mr: 2, ...(drawerOpen && { display: "none" }) }}
              >
                <MenuIcon />
              </IconButton>
              <LogoType />
            </Stack>

            <AppBarActions />
          </Stack>
        </Toolbar>
      </AppBar>
      <Drawer
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          "& .MuiDrawer-paper": {
            width: drawerWidth,
            boxSizing: "border-box",
          },
        }}
        variant="persistent"
        anchor="left"
        open={drawerOpen}
      >
        <DrawerHeader>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === "ltr" ? (
              <ChevronLeftIcon />
            ) : (
              <ChevronRightIcon />
            )}
          </IconButton>
        </DrawerHeader>
        <Divider />
        <VaultList subheader={true} />
      </Drawer>
      <Main open={drawerOpen}>
        <DrawerHeader />

        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/new" element={<NewVault />} />
          <Route path="/vault/:vaultId/new" element={<NewSecret />} />
          <Route
            path="/vault/:vaultId/new/account"
            element={<SecretAccount />}
          />
          <Route path="/vault/:vaultId/new/note" element={<SecretNote />} />
          <Route path="/vault/:vaultId/new/list" element={<SecretList />} />
          <Route path="/vault/:vaultId/new/file" element={<SecretFile />} />
          <Route path="/vault/:vaultId/edit" element={<VaultEdit />} />
          <Route
            path="/vault/:vaultId/:secretId/edit"
            element={<EditSecret />}
          />
          <Route path="/vault/:vaultId/:secretId" element={<Secret />} />
          <Route path="/vault/:vaultId" element={<Vault />} />
          <Route path="*" element={<NotFound />} />
        </Routes>

        <Changes navigate={navigate} />
        <BeforeUnload />
        <Dialogs worker={worker} />
      </Main>
    </Box>
  );
}
