import React from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";

import {
  IconButton,
  Typography,
  Paper,
  Stack,
  Link,
  Box,
  Divider,
  Tooltip,
} from "@mui/material";

import CloseIcon from "@mui/icons-material/Close";

import { AppDispatch } from "../store";
import { setSnackbar } from "../store/snackbar";

import { CONFIRM_DELETE_SECRET, setDialogVisible } from "../store/dialogs";

import {
  SecretMeta,
  SecretKind,
  SecretKindLabel,
  Secret,
  AccountSecret,
  NoteSecret,
  FileSecret,
  CredentialsSecret,
} from "../types";
import { SecretProps } from "../props";
import {
  download,
  humanFileSize,
  sortCredentials,
  fromSerdeDate,
  toDateTime,
  copyToClipboard,
} from "../utils";

import SecretList from "./secret-list";
import VaultHeader from "./vault-header";
import NewSecretFab from "./new-secret-fab";
import ReadOnlyPassword from "./forms/readonly-password";

import { withVaultAndSecret } from "./with-vault";

function SecretHeader(props: SecretProps) {
  const navigate = useNavigate();

  const { storage, meta } = props;
  const label = SecretKindLabel.toString(meta.kind);

  const closeSecret = () => {
    navigate(`/vault/${storage.summary.id}`);
  };

  const lastUpdated = fromSerdeDate(meta.lastUpdated);
  const lastUpdatedDateTime = toDateTime(lastUpdated);

  return (
    <Stack direction="row" spacing={2} marginBottom={1}>
      <Stack flex={1}>
        <Typography variant="h5" component="div">
          {meta.label}
        </Typography>
        <Typography
          variant="caption"
          gutterBottom
          component="div"
          color="text.secondary"
        >
          {label} on {lastUpdatedDateTime}
        </Typography>
      </Stack>

      <Tooltip title="Close this secret" placement="left">
        <IconButton sx={{ width: 40, height: 40 }} onClick={closeSecret}>
          <CloseIcon />
        </IconButton>
      </Tooltip>
    </Stack>
  );
}

type SecretItemProps = {
  meta: SecretMeta;
  secret: Secret;
};

function AccountSecretView(props: SecretItemProps) {
  const { secret } = props;
  const { account, url, password } = secret as AccountSecret;

  return (
    <Paper>
      <Stack spacing={2} padding={2}>
        <Typography variant="subtitle1" component="div">
          {account}
        </Typography>
        {url ? (
          <Link href={url} rel="noreferrer" target="_blank" underline="hover">
            {url}
          </Link>
        ) : null}
        <ReadOnlyPassword value={password} showCopy={false} />
      </Stack>
    </Paper>
  );
}

function NoteSecretView(props: SecretItemProps) {
  const secret = props.secret as NoteSecret;
  return (
    <Paper>
      <Box padding={2}>
        <Typography variant="body1" component="div" color="text.secondary">
          {secret}
        </Typography>
      </Box>
    </Paper>
  );
}

function FileSecretView(props: SecretItemProps) {
  const { secret } = props;
  const { name, buffer, mime } = secret as FileSecret;
  return (
    <Paper>
      <Stack padding={2}>
        <Typography variant="body1" component="div">
          {name}
        </Typography>
        <Typography variant="body1" component="div" color="text.secondary">
          {mime}
        </Typography>
        <Typography variant="body2" component="div" color="text.secondary">
          {humanFileSize(buffer.length)}
        </Typography>
      </Stack>
    </Paper>
  );
}

function CredentialsSecretView(props: SecretItemProps) {
  const secret = props.secret as CredentialsSecret;

  // Sort for deterministic ordering
  const list = sortCredentials(secret);

  return (
    <Paper>
      <Box padding={2}>
        {list.map((item: [string, string], index: number) => {
          const [name, value] = item;
          return (
            <Stack
              key={index}
              direction="row"
              paddingBottom={1}
              alignItems="center"
            >
              <Typography variant="body1" component="div" flex={1}>
                {name}
              </Typography>
              <ReadOnlyPassword value={value} showCopy={true} />
            </Stack>
          );
        })}
      </Box>
    </Paper>
  );
}

type SecretActionsProps = SecretProps & { children?: React.ReactNode };

function SecretActions(props: SecretActionsProps) {
  const dispatch = useDispatch();

  const { vaultId, secretId, meta } = props;

  const onDelete = (event: React.MouseEvent<HTMLElement>) => {
    event.preventDefault();
    const { label } = meta;
    dispatch(
      setDialogVisible([CONFIRM_DELETE_SECRET, true, { label, secretId }])
    );
  };

  return (
    <Box
      sx={{
        "& > :not(style)": {
          ml: 1,
        },
      }}
    >
      <Tooltip title="Edit this secret">
        <Link variant="body2" href={`#/vault/${vaultId}/${secretId}/edit`}>
          Edit
        </Link>
      </Tooltip>
      <Tooltip title="Delete this secret">
        <Link variant="body2" href="#" onClick={onDelete}>
          Delete
        </Link>
      </Tooltip>
      {props.children}
    </Box>
  );
}

function SecretLayout(props: SecretProps) {
  const dispatch: AppDispatch = useDispatch();
  const { meta, secret } = props;

  const getSecretView = () => {
    switch (meta.kind) {
      case SecretKind.Account:
        return <AccountSecretView meta={meta} secret={secret} />;
      case SecretKind.Note:
        return <NoteSecretView meta={meta} secret={secret} />;
      case SecretKind.Credentials:
        return <CredentialsSecretView meta={meta} secret={secret} />;
      case SecretKind.File:
        return <FileSecretView meta={meta} secret={secret} />;
    }
  };

  const getSecretActions = () => {
    const onCopy = async (
      event: React.MouseEvent<HTMLElement>,
      value: string
    ) => {
      event.preventDefault();
      await copyToClipboard(value);
      dispatch(
        setSnackbar({
          message: "Copied to clipboard",
          severity: "success",
        })
      );
    };

    const onDownloadFile = (event: React.MouseEvent<HTMLElement>) => {
      event.preventDefault();
      const { name, buffer, mime } = secret as FileSecret;
      download(name, new Uint8Array(buffer), mime);
    };

    switch (meta.kind) {
      case SecretKind.Account:
        return (
          <Tooltip title="Copy to clipboard">
            <Link
              href="#"
              variant="body2"
              onClick={(e) => {
                const { password } = secret as AccountSecret;
                onCopy(e, password);
              }}
            >
              Copy
            </Link>
          </Tooltip>
        );
      case SecretKind.Note:
        return (
          <Tooltip title="Copy to clipboard">
            <Link
              href="#"
              variant="body2"
              onClick={(e) => {
                onCopy(e, secret as string);
              }}
            >
              Copy
            </Link>
          </Tooltip>
        );
      case SecretKind.File:
        return (
          <Tooltip title="Download this file">
            <Link href="#" variant="body2" onClick={onDownloadFile}>
              Download
            </Link>
          </Tooltip>
        );
      case SecretKind.Credentials:
        break;
    }
  };

  return (
    <Stack padding={3} spacing={1}>
      <SecretHeader {...props} />
      <Divider />
      <Box paddingTop={1} paddingBottom={1}>
        {getSecretView()}
      </Box>
      <SecretActions {...props}>{getSecretActions()}</SecretActions>
    </Stack>
  );
}

function Unlocked(props: SecretProps) {
  const { account, worker, storage, vaultId, secretId } = props;

  return (
    <>
      <VaultHeader editing={false} storage={storage} worker={worker} />
      <Stack>
        <SecretLayout {...props} />
        <Divider />
        <SecretList
          account={account}
          worker={worker}
          storage={storage}
          vaultId={vaultId}
          secretId={secretId}
        />
      </Stack>
      <NewSecretFab storage={storage} />
    </>
  );
}

export default withVaultAndSecret(Unlocked);
