import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { useParams } from "react-router-dom";

import {
  Stack,
  Typography,
  Card,
  CardContent,
  CardActions,
  Button,
} from "@mui/material";

import { WorkerStorageProps } from "../props";
import { AppDispatch } from "../store";
import { accountSelector, refresh } from "../store/account";
import { vaultsSelector } from "../store/vaults";
import {
  setDialogVisible,
  VERIFY_PASSWORD,
  CONFIRM_DELETE_VAULT,
  CHOOSE_PASSPHRASE,
} from "../store/dialogs";
import { WorkerContext } from "../worker-provider";

import NotFound from "../not-found";
import NotAuthorized from "../not-authorized";
import VaultHeader from "./vault-header";

import NameForm from "./forms/name-form";

import { download } from "../utils";

type VaultEditActionsProps = WorkerStorageProps;

function VaultEditActions(props: VaultEditActionsProps) {
  const dispatch: AppDispatch = useDispatch();
  const { account } = useSelector(accountSelector);
  const { cache } = account;

  const { storage } = props;
  const { summary } = storage;

  const onRenameVault = async (name: string) => {
    await cache.renameVault(summary, name);
    await dispatch(refresh({ cache }));
  };

  const onDownload = async () => {
    const buffer = await cache.buffer();
    download(`${summary.id}.vault`, buffer);
  };

  const onChangePassphrase = async () => {
    const onVerified = async (passphrase: string) => {
      const onNewPassphrase = async (newPassphrase: string) => {
        await cache.changePassphrase(passphrase, newPassphrase);
      };
      await dispatch(
        setDialogVisible([CHOOSE_PASSPHRASE, true, onNewPassphrase])
      );
    };
    await dispatch(setDialogVisible([VERIFY_PASSWORD, true, onVerified]));
  };

  const onDeleteVault = async () => {
    const onVerified = async () => {
      await dispatch(setDialogVisible([CONFIRM_DELETE_VAULT, true, summary]));
    };
    await dispatch(setDialogVisible([VERIFY_PASSWORD, true, onVerified]));
  };

  return (
    <Stack padding={3} spacing={4}>
      <Card>
        <CardContent>
          <Typography variant="h6">Rename</Typography>
          <Typography variant="body1" color="text.secondary">
            Change the name of this vault.
          </Typography>

          <NameForm name={summary.name} onFormSubmit={onRenameVault} />
        </CardContent>
        <CardActions>
          <Button type="submit" form="name-form">
            Set Vault Name
          </Button>
        </CardActions>
      </Card>

      <Card>
        <CardContent>
          <Typography variant="h6">Download</Typography>
          <Typography variant="body1" color="text.secondary">
            Export a copy of the encrypted vault for backup or archiving.
          </Typography>
        </CardContent>
        <CardActions>
          <Button onClick={() => onDownload()}>Download</Button>
        </CardActions>
      </Card>

      <Card>
        <CardContent>
          <Typography variant="h6">Encryption Passphrase</Typography>
          <Typography variant="body1" color="text.secondary">
            Change the encryption passphrase for this vault.
          </Typography>
        </CardContent>
        <CardActions>
          <Button onClick={() => onChangePassphrase()} color="error">
            Update Passphrase
          </Button>
        </CardActions>
      </Card>

      <Card>
        <CardContent>
          <Typography variant="h6">Delete Vault</Typography>
          <Typography variant="body1" color="text.secondary">
            Permananently delete this vault; once you delete a vault there is no
            going back.
          </Typography>
        </CardContent>
        <CardActions>
          <Button onClick={() => onDeleteVault()} color="error">
            Delete Vault
          </Button>
        </CardActions>
      </Card>
    </Stack>
  );
}

export default function VaultEdit() {
  const params = useParams();
  const { vaultId } = params;
  const { vaults } = useSelector(vaultsSelector);

  const storage = vaults.find((v) => v.summary.id === vaultId);

  if (!storage) {
    return <NotFound />;
  }

  return (
    <WorkerContext.Consumer>
      {(worker) => {
        return storage.locked ? (
          <NotAuthorized />
        ) : (
          <Stack>
            <VaultHeader editing={true} worker={worker} storage={storage} />
            <VaultEditActions worker={worker} storage={storage} />
          </Stack>
        );
      }}
    </WorkerContext.Consumer>
  );
}
