import React, { useState } from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from "@mui/material";

import Diceware from "../../diceware";

import { VaultWorker } from "../../types";

interface ChoosePassphraseProps {
  worker: VaultWorker;
  open: boolean;
  handleCancel: () => void;
  handleOk: (result: string) => void;
}

export default function ChoosePassphraseDialog(props: ChoosePassphraseProps) {
  const { worker, open, handleCancel, handleOk } = props;
  const [passphrase, setPassphrase] = useState("");

  const onSubmit = () => handleOk(passphrase);
  const onGenerate = (passphrase: string) => setPassphrase(passphrase);

  return (
    <Dialog open={open} onClose={handleCancel}>
      <DialogTitle color="text.secondary">Choose Passphrase</DialogTitle>
      <DialogContent>
        <Diceware onGenerate={onGenerate} worker={worker} />
      </DialogContent>
      <DialogActions>
        <Button onClick={handleCancel}>Cancel</Button>
        <Button onClick={onSubmit} variant="contained">
          OK
        </Button>
      </DialogActions>
    </Dialog>
  );
}
