/* eslint-disable react/display-name */
import React, { useState, useEffect } from "react";

import { useSelector, useDispatch } from "react-redux";
import { useParams, useSearchParams } from "react-router-dom";

import { WorkerContext } from "../worker-provider";
import { AppDispatch } from "../store";
import { AccountProps, VaultProps, SecretProps } from "../props";
import { vaultsSelector, readSecret } from "../store/vaults";
import { accountSelector } from "../store/account";
import VaultLocked from "./vault-locked";

// Higher order component to remove some of the boilerplate
// for views that need an account reference.
export function withAccount(
  WrappedComponent: (props: AccountProps) => JSX.Element
) {
  return function () {
    const { account } = useSelector(accountSelector);
    return (
      <WorkerContext.Consumer>
        {(worker) => {
          return <WrappedComponent account={account} worker={worker} />;
        }}
      </WorkerContext.Consumer>
    );
  };
}

// Higher order component to remove some of the boilerplate
// for views that need a vault storage reference.
export function withVault(
  WrappedComponent: (props: VaultProps) => JSX.Element
) {
  return function () {
    const params = useParams();
    const { vaultId } = params;
    const { vaults } = useSelector(vaultsSelector);
    const { account } = useSelector(accountSelector);
    const storage = vaults.find((v) => v.summary.id === vaultId);
    if (!storage) {
      return null;
    }
    return (
      <WorkerContext.Consumer>
        {(worker) => {
          return storage.locked ? (
            <VaultLocked worker={worker} storage={storage} />
          ) : (
            <WrappedComponent
              account={account}
              vaultId={vaultId}
              worker={worker}
              storage={storage}
            />
          );
        }}
      </WorkerContext.Consumer>
    );
  };
}

// Higher order component to remove some of the boilerplate
// for views that need a vault storage and secret reference.
export function withVaultAndSecret(
  WrappedComponent: (props: SecretProps) => JSX.Element
) {
  return function () {
    const params = useParams();
    const dispatch = useDispatch<AppDispatch>();
    const [searchParams] = useSearchParams();
    const { vaultId, secretId } = params;
    const { vaults } = useSelector(vaultsSelector);
    const { account } = useSelector(accountSelector);
    const [secretData, setSecretData] = useState(null);
    const storage = vaults.find((v) => v.summary.id === vaultId);

    useEffect(() => {
      const loadSecret = async () => {
        try {
          const result = await dispatch(
            readSecret({ account, storage, secretId })
          );
          if (result.payload) {
            setSecretData(result.payload);
          }
        } catch (e) {
          console.error("Error reading secret", e);
        }
      };

      if (storage) {
        loadSecret();
      }
    }, [secretId, searchParams]);

    if (!storage || !secretData) {
      return null;
    }

    const [meta, secret] = secretData;

    return (
      <>
        <WorkerContext.Consumer>
          {(worker) => {
            return storage.locked ? (
              <VaultLocked worker={worker} storage={storage} />
            ) : (
              <WrappedComponent
                account={account}
                vaultId={vaultId}
                secretId={secretId}
                secret={secret}
                meta={meta}
                worker={worker}
                storage={storage}
              />
            );
          }}
        </WorkerContext.Consumer>
      </>
    );
  };
}
